.TvShowGrid {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  display: grid;
  grid-template-columns: minmax(135px, auto) minmax(135px, auto) minmax(135px, auto);
  grid-auto-rows: 456px;
  justify-items: center;
}

.TvShow > img {
  max-height: 300px;
  object-fit: scale-down;
  display:inline-block;
}

.TvShow > h3 {
  height: 90px;
}

.TvShowHeader {
  margin: 0px;
  padding: 15px;
  background-color: #f1f1f1;
}
